.tariff-info {
    width: 100%;
    background-color: var(--white);
    box-shadow: var(--shadow-primary);
    border-radius: 12px;


    color: var(--dark);
    display: grid;
    align-items: center;
    grid-column-gap: 32px;
    grid-row-gap: 8px;
    grid-template-columns: percent(250, 1180) percent(275, 1180) percent(175, 1180) percent(150, 1180) 1fr;
    padding: 32px 24px;

    -webkit-tap-highlight-color: transparent;
    transition: box-shadow $transitionSpeed $transitionFunction;
    text-align: start;
    div {
        &:nth-child(1) {
            display: flex;
            align-items: center;
            .avatar{
                width: 40px;
                height: 40px;
            }
            span{
                &:nth-child(1){
                    margin-right: 16px;
                }
            }
        }
    }
    >span{
        &:last-child{
            color: var(--blue);
          justify-self: end;
        }
    }
    @media (min-width:$desktop) {
        &:hover {
            &:not(.client-info--no-animate) {
                box-shadow: var(--shadow-secondary);
            }
        }
    }
}

