.tariff-card {
    .tariff-card-filter__row--1 {
        grid-template-columns: percent(300, 1180) percent(432, 1180) percent(432, 1180);
    }

    .tariff-card-filter__row--2 {
        grid-template-columns: percent(740, 1180)
    }

    .selection-estate__controls {
        margin-top: 0;
    }

    .main-table__type {
        padding: 0 24px;
        grid-template-columns: percent(250, 1180) percent(275, 1180) percent(175, 1180) percent(150, 1180) 1fr;
        grid-column-gap: 32px;

        span {
            &:first-child {
                margin: 0;
            }
            &:not(:first-child){
                text-align: left;
                margin-left: 20px;
            }
        }
    }

    &__table{
        &--one{
            .main-table__type{
                grid-template-columns: percent(250, 1180) percent(275, 1180) percent(175, 1180) percent(150, 1180) 1fr;
            }
        }
        &--two{
   .main-table__type{
    grid-template-columns: percent(225, 1180) percent(225, 1180) 1fr;
            }
        }
    }
}
